<template>
  <div class="head">
    <div class="menu">
      menu
      <a href="javaScript:;" id="icon_menu" @click="isShowMenuFlag">
        <i class="icon icon_menu" v-show="!menuFlag"></i>
        <i class="icon icon_out" v-show="menuFlag"></i>
      </a>
    </div>
    <div class="menu_list" v-show="menuFlag">
      <ul>
        <!-- router-link直接添加click会无效,要设置native才会生效 -->
        <router-link tag="li" @click.native="isShowMenuFlag" active-class="current" to="/index"><a
            href="javascrpt:;">首页&nbsp;/&nbsp;index</a></router-link>
        <router-link tag="li" @click.native="isShowMenuFlag" active-class="current" to="/work"><a
            href="javascrpt:;">作品&nbsp;/&nbsp;work</a></router-link>
        <router-link tag="li" @click.native="isShowMenuFlag" active-class="current" to="/blog"><a
            href="javascrpt:;">博客&nbsp;/&nbsp;blog</a></router-link>
        <router-link tag="li" @click.native="isShowMenuFlag" active-class="current" to="/photo"><a
            href="javascrpt:;">相册&nbsp;/&nbsp;photo</a></router-link>
        <router-link tag="li" @click.native="isShowMenuFlag" active-class="current" to="/login"><a
            href="javascrpt:;">聊天&nbsp;/&nbsp;chat</a></router-link>
        <router-link tag="li" @click.native="isShowMenuFlag" active-class="current" to="/contact"><a
            href="javascrpt:;">联系&nbsp;/&nbsp;contact</a></router-link>
        <router-link tag="li" @click.native="isShowMenuFlag" active-class="current" to="/collection"><a
            href="javascrpt:;">收藏&nbsp;/&nbsp;collection</a></router-link>
      </ul>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { mapState, mapActions } from "vuex";
import "../assets/js/common";
export default {
  computed: {
    ...mapState(["menuFlag"])
  },
  methods: {
    ...mapActions(["isShowMenuFlag"])
  }
};
</script>
<style scoped>
@import "../assets/css/common.css";
</style>


