import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
let store = new Vuex.Store({
    state: {
        menuFlag:false
    },
    getters: {

    },
    mutations: {
        //显示隐藏菜单栏
       isShowMenu(state){
           state.menuFlag=!state.menuFlag;
        //    console.log("我被执行了");
           
       }
    },

    //异步actions
    actions: {
        isShowMenuFlag(context){
            context.commit("isShowMenu")
      }
    }
})
export default store;


