import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

let router = new Router({
  // linkActiveClass: 'current',
  // linkExactActiveClass: 'current',
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: () => import("@/pages/index"),
      meta: {
        isShowHead: true,
        keepAlive: false //需要被缓存的组件
      },

    },
    //聊天室登录
    {
      path: '/login',
      name: 'Login',
      component: () => import("@/pages/login")
    },
    //聊天室界面
    {
      path: '/chat/:id?',
      name: 'Chat',
      component: () => import("@/pages/chat"),
      // redirect: {path:"/login"}
    },
    //登录到聊天室
    //留言板
    {
      path: '/contact',
      name: 'Contact',
      component: () => import("@/pages/contact"),
      meta: {
        isShowHead: true
      }
    },
    //相册
    {
      path: '/photo',
      name: 'Photo',
      component: () => import("@/pages/photo"),
      meta: {
        isShowHead: true
      }
    },
    //作品
    {
      path: '/work',
      name: 'Work',
      component: () => import("@/pages/work"),
      meta: {
        isShowHead: true
      },
    },
    {
      path: '/collection',
      name: 'Collection',
      component: () => import("@/pages/collection"),
      meta: {
        isShowHead: true
      },
    },
    {
      path: '/demo/bcbk',
      name: 'Bcbk',
      component: () => import("@/pages/bcbk"),
    },
    {
      path: '/*',
      name: 'Error',
      redirect: "/index"
    },
  ]
})
export default router



